<script>
import vue, {lib, library} from "vue.min.js"
export default vue.component ({
	id: "media-query:progress",
	attribute: [],
	setup: function () {
		return {vue}
		},
	method: {
		progress: function () {
			if (vue.on.progress.spin ()) return "on"
			else return "off"
			},
		},
	})
</script>

<template id="media-query:progress" type="component">
	<div id="media-query:progress" v-bind:aria-data="progress ()">
		{{ vue.on.progress.data }}
	</div>
</template>
