import vue, {lib, library} from "vue.min.js"
import ini from "@/package.json"
let {define} = lib
let {zero, one} = lib

vue.fire = function () {}
vue.fire.base = new lib.google.fire.base
vue.fire.base.setup = function (firebase) {
	vue.fire.base.start (firebase)
	vue.fire.base.auth.state (function (account) {
		if (account) {}
		else {}
		})
	}

vue.on ("package", function () {
	var firebase
	vue.then (function () {
		if (vue.package.fire.base.project) vue.fire.base.setup (firebase = vue.package.fire.base)
		})
	vue.catch (function () {
		if (firebase = vue.config ("fire:base")) if (vue.config ("fire:base").project) vue.fire.base.setup (firebase)
		})
	})
